import React from 'react';
import styled from "styled-components";
import "../App.css";
import picture from "../images/main_picture.png";
import texture from "../images/background/natural-paper.png";
import Fade from "react-reveal/Fade";

const StyledContainer = styled.div`
    background-color: #f5d7d0;
        background-image: url(${texture});
`;
const StyledImg = styled.img`
    width: 100%;
`;

const TextDiv = styled.div`
    color: #e4e4e4;
    font-size:200%;
    font-family: "Andalusia";
    font-weight: 200;
    text-align: center;
    padding-top: 5%;
`;

const Contents = styled.div`
    font-size: 90%;
    text-align: center;
    color: #424242;
    font-family: "MaruBuri";
    font-weight: 100;
    padding-bottom: 10%;
`;

export default function TopContainer2() {
    return (
        <StyledContainer>

            <StyledImg src={picture}/>
            <Fade bottom>
                <Contents>
                    <div>2022년 9월 18일 일요일 오후 3시</div>
                    <div>소노펠리체 컨벤션 다이아몬드홀 (단독홀)</div>
                </Contents>
            </Fade>
        </StyledContainer>
    )
        ;
}